body {
    font-family: "Mont";
}


html{
    scroll-behavior: smooth;
}

.main-section{
    position: relative;
}

.container{
    max-width: 1320px;
    width: 100%;
    margin: 0 auto;
    padding: 0 20px 0 110px;

    @include media ("<tablet"){
        padding: 0 20px;
    }
    
}

.button{
    background: #3C467A;
    padding: 10px;
    color: #FFFFFF;
    font-weight: 800;
    font-size: 20px;

    &:hover{
        background: #272f58;
    }
}

.main-section{
    padding: 30px 0 70px 0;

    @include media ("<desktop"){
        padding: 40px 0 35px 0;
    }
}


.links{
    display: flex;
    flex-direction: column;
    position: fixed;
    margin-top: -30px;

    @include media ("<tablet"){
        display: none;
    }

    .item-link{
        padding-left: 55px;

        a{
            writing-mode: vertical-lr;
            padding-bottom: 30px;
            color: #3C467A;
            text-transform: uppercase;
            transform: rotate(180deg);
        }

        .arrow{
            margin-left: 4px;

            img{
                transform: rotate(180deg);
            }
        }
    }
}

.main-text{
    h1{
        @include adaptiv-vaule("max-width", 1000, 305, 1);
        line-height: 120%;
        color: #3C467A;
        margin-bottom: 20px;

    }

    h3{
        margin-bottom: 40px;

        @include media ("<tablet"){
            font-size: 14px;
        }

    }
}

.main-img{
    width:100%;
    height: 518px;
    background: url(../images/mainimg.jpg) 0 0/cover no-repeat;
    margin-bottom: 75px;
    display: flex;
    flex-direction: column;
    justify-content: center;

    @include media("<desktop"){
        margin-bottom: 40px;
    }

    @include media("<phone"){
        justify-content: flex-end;
        margin-bottom: 50px;
    }

    .main-img-text{
        display: flex;
        flex-direction: column;
        padding-left: 100px;

        @include media("<phone"){
            padding-left: 33px;
        }

        p{
            color: #FFFFFF;
            font-size: 14px;
        }

        h2{
            color: #FFFFFF;
            margin-bottom: 70px;
            font-size: 52px;
            font-weight: 600;
            line-height: 62px;
            max-width: 472px;
            width: 100%;

            @include media("<tablet"){
                font-size:36px;
                margin-bottom:33px;
                max-width: 472px;
                padding-right: 10px;
            }
        }

        a{
            color: #FFFFFF;
            font-size: 14px;
            
            @include media("<phone"){
                margin-bottom: 60px;
                text-decoration: underline;
            }
            
        }
    }
}

.main-info{
    margin-bottom:35px;

    p{
        font-size: 17px;
        line-height: 120%;
        text-align: center;
        max-width: 625px;
        width: 100%;
        margin: 0 auto;

        @include media ("<tablet"){
            font-size: 14px;
        }
    }
}

.main-button{
    display: flex;
    justify-content: center;

    .button{
        padding: 15px 60px;
    }
}

.about-service{
    padding: 0 0 80px 0;

    @include media ("<desktop"){
        position: relative;
    }

    h3{
      font-size: 17px;
      font-weight: 800;  
      text-transform: uppercase;
      @include adaptiv-vaule("margin-bottom", 60 , 15, 1);
    }
}

.service-item-block{
    display: flex;
    justify-content: space-between;
    margin: 0 -20px;
    position: relative;

    .slick-dots{
        display: flex;
        position: absolute;
        bottom: -60px;
        left: 50%;
        transform: translate(-50%);
    }

    .item-block{
        display: flex;
        flex-direction: column;
        padding: 0 20px;

        @include media ("<desktop"){
            align-items: center;
        }
        
        
        .item-content{
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            margin-bottom: 25px;
            max-width:370px;
            min-height: 275px;
            
            img{
                position: absolute;
                z-index: -1;
            }

            p{
                bottom:13px;
                color: #FFFFFF;
                font-size: rem-calc(58px);
                font-weight: 800;
                line-height: 100%;
                letter-spacing: -0.06em;
    
                @include media ("<1360px"){
                    font-size: rem-calc(50px);
                }
    
                @include media ("<widescreen"){
                    font-size: rem-calc(41px);
                }
            }
        }
    }
}

.item-button{
    
    a{
        font-size: 14px;
        font-weight: 800;
        border-bottom: 1px solid #000;
        
    }

    img{
        vertical-align: middle;
        padding-left: 10px;
    }
}

.slider{
    position: relative;
}

.slick-track{
    display: flex;
}

.slick-list{
    overflow: hidden;
}

.slick-dots{
    display: flex;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%);
    padding: 0;

    button{
        font-size: 0;
        width:15px;
        height: 15px;
        border-radius: 50%;
        background-color:transparent;
        border: none;
        
    }

    .slick-active{
        
        button{
            background-color: #3C467A;
            border:1px solid #C4C4C4;
        } 
    } 

    li{
        list-style: none;
        margin: 0 10px;
        border:1px solid #C4C4C4;
        border-radius: 50%;
        padding: 2px 2px 17px 2px;
        height:20px;
        display: flex;
        
    }
}

.challenges{
    padding: 0 0 100px 0;

    h3{
        font-size: 17px;
        font-weight: 800;
        text-transform: uppercase;
        margin-bottom: 100px;
    }

    ol{
        display: flex;
        flex-wrap: wrap;
        list-style: none;
        counter-reset: my-awesome-counter;
        padding: 0;
        margin: 0;

        @include media("<desktop"){
            flex-direction:column;
        }

        li{
            counter-increment: my-awesome-counter;
            display: flex;
            align-items: flex-start;
            flex: 0 1 50%;
            margin-bottom: 90px;
            list-style: none;

            
            &:nth-child(odd){
                padding-right: 20px;
            }
            
            &:nth-child(-n+6)::before {
                content: "0" counter(my-awesome-counter);
            }
            
            &::before{
                content: counter(my-awesome-counter);
                font-weight: bold;
                font-size: 80px;
                margin-right: 0.5rem;
                line-height: 1;
                color: #3C467A;
            }

            @include media("<desktop"){
    
                &:nth-child(even){
                    flex-direction:row-reverse;
                }
            }

            p{
                font-size: 17px;
                max-width: 365px;
                margin-top: 7px;

                @include media ("<tablet"){
                    font-size: 13px;
                }
            }
        }
    }
}

.project{
    padding: 0 0 70px 0;

    @include media ("<tablet"){
        max-width:100vw;
    }

    .container{

        @include media ("<phone"){
            padding: 0;
        }
    }
}

.project-content{
    display: grid;
    row-gap:75px;
    column-gap: 80px ;
    grid-template-columns: 1fr 1fr;

    @include media ("<desktop"){
        grid-template-columns:1fr;
        display: flex;
        flex-direction: column;
        row-gap:0;
        align-items:stretch;
    }
}


.project-text{
    display: grid;
    grid-auto-flow: row;
    grid-template:0.1fr 0.1fr / 1fr;
    align-self: flex-start;
    margin-bottom: 75px;

    @include media ("<tablet"){
        padding-left: 20px;
        margin-bottom: 0;
    }

    h3{
        margin-bottom: 12px;
        font-size: 17px;
        text-transform: uppercase;
    }

    p{
        font-size: 18px;
        max-width: 400px;
        margin-bottom: 45px;
        line-height: 130%;
        font-weight: 600;
    }

}
.project-button{

    @include media ("<tablet"){
        display: none;
    }

    .button{
        padding: 15px 35px;
    }

    img{
        vertical-align: 3px;
        padding-left: 20px;
    }
}
.block{

    grid-row: span 2;

    @include media("<desktop"){
        margin-bottom: 20px;
    }

    @include media("<tablet"){
        margin-bottom:0;
    }
    
