        }
    }

    p{
        @include media("<desktop"){
            font-size: 10px;
        }
        @include media ("<tablet"){
            display: none;
        }
    }

    .block-img{
        position: relative;
        max-width:554px;
        width:100%;
        min-height: 554px;
        margin: 0 0 20px 0;

        @include media ("<desktop"){
            max-width:100vw;
        }

        @include media ("<tablet"){
            margin: 0;
        }
        

        img{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .block-text{
        
        h2{
            @include media ("<tablet"){
                display: block;
                position: absolute;
                bottom: 130px;
                left: 30px;
                color: #FFFFFF;
                font-size: 36px;
            }
        }

        p{
            @include media ("<tablet"){
